<template>
  <v-layout row wrap>
    <v-flex xs6>
      <v-card class="openFaas">
        <v-layout>
          <v-flex xs3 justify-center>
            <v-img
              lazy-src="data:image/webp;base64,"
              src="https://raw.githubusercontent.com/openfaas/media/master/OpenFaaS_Watch_Face.png"
              height="120px"
              contain
            ></v-img>
          </v-flex>
          <v-flex xs9>
            <v-card-title primary-title>
              <div>
                <div class="headline"> {{ functions.count }} </div>
                <div class="headline">Available OpenFaas functions</div>
              </div>
            </v-card-title>
          </v-flex>
        </v-layout>
      </v-card>
    </v-flex>
    <v-flex xs6>
      <v-card color="grey" class="white--text">
        <v-layout>
          <v-flex xs3 justify-center>
            <v-img
              lazy-src="data:image/webp;base64,"
              src="https://www.minio.io/img/logo/logo_white.svg"
              height="120px"
              contain
            ></v-img>
          </v-flex>
          <v-flex xs9>
            <v-card-title primary-title>
              <div>
                <div class="headline">{{storage.bucketsCount}}</div>
                <div class="headline">Available Minio buckets</div>
              </div>
            </v-card-title>
          </v-flex>
        </v-layout>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from 'axios'
export default {
  name: 'dashboard',
  props: {
    minioClient: {},
    openFaaS: {}
  },
  data: () => {
    return {
      storage: {
        bucketsCount: 0
      },
      functions: {
        count: 0
      }
    }
  },
  methods: {
    // TODO: Refactor these methods to obtain the values of events between components
    functionsCount () {
      var params = { 'type': 'load', 'url': this.openFaaS.endpoint }
      axios({ method: 'post', url: '$VUE_APP_BACKEND_HOST', data: params })
        .then((response) => {
          // handle success
          this.functions.count = response.data.length
        })
        .catch((error) => {
          // handle error          
          window.getApp.$emit('APP_SHOW_SNACKBAR', { text: error.response.data, color: 'error' })
        })
    },
    bucketsCount () {
      this.minioClient.listBuckets((err, obtainedBuckets) => {
        if (err) return window.getApp.$emit('APP_SHOW_SNACKBAR', { text: err.message, color: 'error' })
        this.storage.bucketsCount = obtainedBuckets.length
      })
    }
  },
  created: function () {
    this.functionsCount()
    this.bucketsCount()
  }
}
</script>

